.navigation__list {
	@include reset-list;
	display: flex;
	flex-wrap: wrap;
	margin-top: 20px;
	user-select: none;
}

.navigation__item {
	margin-right: 64px;
	margin-bottom: 20px;

	&:last-child {
		margin-right: 0;
	}
}


.navigation__link {
	color: #06A9B3;
	text-decoration: none;
}